import {useRef,useEffect,useState} from 'react'
import { Button, Grid } from "semantic-ui-react"
import styled from "styled-components"

const StyledDetailCost = styled.div({
    boxSizing:'border-box',
    border:'1px solid #CCCCCC',
    padding:'20px',
    borderRadius:'10px',
})

const StyledHeader = styled.h4({
    fontSize:'24px',
    fontWeight:'normal',
})

const StyledInnerHeader = styled.h4({
    fontSize:'20px',
    color:'#444444',
    display:'block',
    marginBottom:'4%',
    marginTop:'4%',
    fontWeight:'normal',
})
  

const BeneficalType = styled.div({
    marginTop:'5%',
})

const CostButton = styled.button({
    margin:'auto',
    marginBottom:'3%',
    border:'1px solid #CCCCCC',
    fontSize:'16px',
    padding:'2%',
    borderRadius:'10px',
    cursor:'pointer',
    fontWeight:'lighter',
    backgroundColor:'white',
})

const CareLevel = styled.div({
    
})

const ResCost = styled.div`
    
    margin-top:5%;
    background-color:#f15aa3;
    border-radius:10px;
    color:white;
    width:100%;
    
`
const ResCostText = styled.div`
    display:inline-block;
    font-size:20px;
    font-weight:400;
    font-style:normal;
    margin:3%;
`

const costArr={
    benetype1:1000,
    benetype2:2000,
    benetype3:3000,
    benetype4:4000,
    carelevel1:3,
    carelevel2:4,
    carelevel3:5,
    carelevel4:6,
    carelevel5:7,
}

function DetailCost(){
    const boxRef = useRef(null);
    const [enterid,setEnterid]= useState(""); //  id  
    const [isEnter,setIsEnter] = useState(false); //   

    /*  */
    const [beneClickid,setBeneClickid]= useState(""); //   id
    const [benePreClick,setBenePreClick] = useState(""); //      id
    const [beneCost,setBeneCost]= useState(0); //   cost

    /* */
    const [careClickid,setCareClickid]=useState(""); //   id
    const [carePreClick,setCarePreClick] = useState(""); //       id
    const [careCost,setCareCost]= useState(0); //   cost

    const onMouseEnter = (i)=>{
        setEnterid(i.target.id);
        setIsEnter(true);
    }
    const onMouseLeave = (i)=>{
        setEnterid(i.target.id);
        setIsEnter(false);
    }
    const beneOnClick = (i) =>{ //    ,                 .
        if(i.target.id===beneClickid)
            return;
        setBeneClickid(i.target.id)
        const item = document.getElementById(i.target.id);
        item.style.color='#496ace';
        item.style.fontWeight='bolder';
        item.style.backgroundColor='#e6edff';
        item.style.border="1px solid #496ace";
        if(benePreClick!==''){
            const preitem = document.getElementById(benePreClick);
            preitem.style.color='black';
            preitem.style.fontWeight='lighter';
            preitem.style.backgroundColor='white';
            preitem.style.border="1px solid #CCCCCC";
        }
        setBenePreClick(i.target.id);
        setBeneCost(costArr[beneClickid]);
    }
    const careOnClick = (i) =>{ //     ,                 .
        if(careClickid===i.target.id)
            return;
        setCareClickid(i.target.id)
        const item = document.getElementById(i.target.id);
        item.style.color='#496ace';
        item.style.fontWeight='bolder';
        item.style.backgroundColor='#e6edff';
        item.style.border="1px solid #496ace";
        if(carePreClick!==''){
            const preitem = document.getElementById(carePreClick);
            preitem.style.color='black';
            preitem.style.fontWeight='lighter';
            preitem.style.backgroundColor='white';
            preitem.style.border="1px solid #CCCCCC";
        }
        setCarePreClick(i.target.id);
    }

    useEffect(()=>{ // enter or leave  
        if(enterid!==''&&(enterid!==beneClickid&&enterid!==careClickid)){
            const item = document.getElementById(enterid);
            if(isEnter) item.style.backgroundColor='#e6edff';
            else {
                item.style.backgroundColor='white'
            }
        }
    },[isEnter])

    //    
    // setCareCost  useEffect  useState  .
    //          useEffect .
    useEffect(()=>{ 
        if(careClickid!=='')
            setCareCost(costArr[careClickid]);
    },[careClickid]);
    useEffect(()=>{
        if(beneClickid!=='')
            setBeneCost(costArr[beneClickid]);
    },[beneClickid]);

    return (
        <StyledDetailCost >
            
            <StyledHeader></StyledHeader>
            <BeneficalType> 
            <StyledInnerHeader> </StyledInnerHeader>
                <CostButton id="benetype1" onClick={beneOnClick} onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave} style={{width:'48%'}}></CostButton>
                <CostButton id="benetype2" onClick={beneOnClick} onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave} style={{width:'48%',float:'right'}}></CostButton>
                <CostButton id="benetype3" onClick={beneOnClick} onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave} style={{width:'100%'}}> 40% </CostButton>
                <CostButton id="benetype4" onClick={beneOnClick} onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave} style={{width:'100%'}}> 60% </CostButton>
            </BeneficalType>
            <CareLevel>
                <StyledInnerHeader></StyledInnerHeader>
                    <CostButton id="carelevel1" onClick={careOnClick} onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave} type="button" style={{width:'30%'}}>1</CostButton>
                    <CostButton id="carelevel2" onClick={careOnClick} onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave} style={{width:'30%',marginLeft:'5%'}}>2</CostButton>
                    <CostButton id="carelevel3" onClick={careOnClick} onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave} style={{width:'30%',float:'right'}}>3</CostButton>
                    <CostButton id="carelevel4" onClick={careOnClick} onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave} style={{width:'30%'}}>4</CostButton>
                    <CostButton id="carelevel5" onClick={careOnClick} onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave} style={{width:'30%',marginLeft:'5%'}}>5</CostButton>
            </CareLevel>
            <ResCost>
                <div style={{borderBottom:"1px dashed #FFFFFF",padding:'4%'}}>
                    <ResCostText style={{marginLeft:'25%'}}>{beneCost+careCost}</ResCostText>
                    <ResCostText >x</ResCostText>
                    <ResCostText >30</ResCostText>
                </div>
                <div style={{padding:'4%'}}>
                    <ResCostText ></ResCostText>
                    <div style={{float:'right',marginRight:'3%',width:'50%',padding:'2%'}}>
                        <ResCostText >210,000</ResCostText>
                    </div>
                </div>
            </ResCost>
        </StyledDetailCost>
    )
}



export default DetailCost;